.wrapper {
    margin: 0 auto
}

.events-table {
    margin-top: 5%;
    margin-bottom: 5%;
}

.events-table div {
    padding: 0.2em;
}

.events-table .table-header {
    position: sticky;
    top: 0;
    background-color: #96a8d7;
    border-radius: 0.2em;
}

.events-table .table-header .counter {
    border: 0;
    background-color: #4a65cc;
    color: #a9b8d7;
    border-radius: 0.5em;
}

.events-table .line .counters {
    display: flex;
}

.events-table .line {
    margin: 0;
}

.events-table .line .level-1 h6 {
    font-weight: 650;
}

.events-table .line .level-2 {
    padding-left: 2em;
}

.events-table .line .level-3 {
    padding-left: 3em;
}

.events-table .line .level-4 {
    padding-left: 4em;
}

.events-table .line .counters .counter {
    min-width: 9%;
    text-align: center;
    border-left: 1px solid #c5c5c5;
}

